<!--  -->
<template>
  <div class="article-list">
    <van-pull-refresh
      v-model="isLoading"
      @refresh="onRefresh"
      :success-text="text"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
      >
        <ArticleItem v-for="(item,index) in list" :article="item" :key="index"></ArticleItem>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticles } from '@/api/article'
import ArticleItem from '@/components/article-item/index.vue'
export default {
  name: 'ArticleList',
  props: {
    channel: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      timestamp: Date.now(),
      error: false,
      isLoading: false,
      text: ''
    }
  },

  components: { ArticleItem },

  computed: {},

  mounted () {},

  created () {},

  updated () {},

  methods: {
    // 刷新
    async onRefresh () {
      try {
        const { data } = await getArticles({
          channel_id: this.channel.id,
          timestamp: Date.now()
        })
        const res = data.data.results // 每一页加载的数据
        const { results } = data.data
        console.log('刷新')
        this.list.unshift(...res)
        this.text = `刷新成功 刷新了${results.length}条数据`
        this.isLoading = false
      } catch (e) {
        this.text = '刷新失败'
        this.isLoading = false
      }
    },
    async onLoad () {
      try {
        const { data } = await getArticles({
          channel_id: this.channel.id,
          timestamp: this.timestamp
        })

        const res = data.data.results // 每一页加载的数据
        const timestamp = data.data.pre_timestamp // 上次加载时间戳
        this.timestamp = timestamp
        this.list = [...this.list, ...res]

        console.log(this.list)
        if (res.length === 0) {
          this.finished = true
        }
      } catch (e) {
        this.error = true
        this.loading = false
      }
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // 判断是否加载完

      // 加载状态结束
      this.loading = false

      // 数据全部加载完成
    }
  }
}
</script>
<style lang="less" scoped>
.article-list {
  height: 79vh;
  overflow-y: auto; //溢出框提供滚动机制
}
</style>
