<!--  -->
<template>
  <div class="home-container">
    <van-nav-bar fixed
      ><van-button
        class="search-btn"
        slot="title"
        round
        type="info"
        size="small"
        icon="search"
      >
        搜索</van-button
      >
    </van-nav-bar>
    <!-- 频道列表 -->
    <van-tabs
      class="channel-tabs"
      v-model="active"
      animated
      swipeable
      @click="tbn"
    >
      <van-tab v-for="item in channels" :title="item.name" :key="item.id"
        ><article-list-vue :channel="item"></article-list-vue
      ></van-tab>

      <div slot="nav-right" class="placeholder"></div>
      <div slot="nav-right" class="hamburger-btn">
        <i class="toutiao toutiao-gengduo"></i>
      </div>
    </van-tabs>
    <!-- 频道列表 -->
  </div>
</template>

<script>
import articleListVue from './components/article-list.vue'
import { Toast } from 'vant'
import { getUserChannel } from '@/api/user'
export default {
  name: 'home',
  data() {
    return {
      active: 2,
      channels: []
    }
  },

  components: {
    articleListVue
  },

  computed: {},

  mounted() {},

  created() {
    this.loadChannels()
  },

  updated() {},

  methods: {
    // 加载频道列表
    async loadChannels() {
      try {
        const res = await getUserChannel()
        this.channels = res.data.data.channels
      } catch (e) {
        console.log('导入频道出错')
        Toast('获取频道失败')
      }
    },
    tbn(e) {
      console.log(e)
      console.log(this.active)
    }
  }
}
</script>
<style lang="less" scoped>
.home-container {
  padding-top: 174px;
  padding-bottom: 92px;
  /deep/ .van-nav-bar__title {
    max-width: 100%;
  }
  .search-btn {
    width: 555px;
    height: 64px;
    background-color: #5babfb;
    border: none;
    font-size: 28px;
    .van-icon-search {
      font-size: 32px;
    }
  }

  /deep/ .channel-tabs {
    // margin-top: 92px;
    .van-tabs__wrap {
      position: fixed;
      top: 92px;
      z-index: 1;
      left: 0;
      right: 0;
      height: 82px;
    }
    .van-list {
      // margin-top: 180px;
    }
    .van-tab {
      border-right: 1px solid #edeff3;
      min-width: 200px;
      //   height: 82px;
      font-size: 30px;
      color: #777777;
      //   padding-right: 66px;
    }
    .van-tab--active {
      color: black;
    }
    .van-tabs__nav {
      padding: 0px;
    }
    .van-tabs__line {
      background-color: #3296fa;
      width: 31px;
      height: 6px;
      //   margin-bottom: 8px;
      bottom: 8px;
    }
    .placeholder {
      width: 66px;
      height: 80px;
      flex-shrink: 0; //不参与flex计算
    }
    .hamburger-btn {
      position: fixed;
      right: 0;
      width: 66px;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: white;
      opacity: 0.5;
      .toutiao {
        font-size: 33px;
      }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 5px;
        height: 100%;
        background-image: url(@/assets/gradient-gray-line.png);
        background-size: contain;
      }
    }
  }
}
</style>
